// Bullets used in application pages
.bullet {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
  &.bullet-xs {
    width: 0.5rem;
    height: 0.5rem;
  }
  &.bullet-sm {
    width: 0.714rem;
    height: 0.714rem;
  }
  &.bullet-lg {
    width: 1.25rem;
    height: 1.25rem;
  }
}

// color adio
.color-radio {
  position: relative;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  .radio-content {
    height: 20px;
    width: 20px;
    display: block;
    border-radius: 50%;
  }
}

// Match Height
.match-height {
  .card {
    height: calc(100% - 2.2rem);
  }
}
